*,
*:before,
*:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body,
html,
.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    font-size: 13px;
}

.attribution {
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: 11px;
    text-align: center;
    margin-top: 3rem;
}

.attribution a {
    color: hsl(228, 45%, 44%);
}

@media only screen and (min-width: 900px) {
    body,
    html,
    .App {
        font-size: 16px;
    }
}